import { customElement, property } from 'lit/decorators.js';
import {
  css,
  CSSResultGroup,
  html,
  LitElement,
  TemplateResult,
  unsafeCSS,
} from 'lit';
import { classMap } from 'lit/directives/class-map.js';
import { styleMap } from 'lit/directives/style-map.js';
import { getColorVar } from '../../utils';
import icons from '@zds/styles/css/icons.css';

@customElement('zwc-icon')
export class ZwcIcon extends LitElement {
  static override styles?: CSSResultGroup | undefined = [
    unsafeCSS(icons),
    css`
      *,
      *::before,
      *::after {
        box-sizing: border-box;
      }

      :host {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        color: var(--zwc-icon-color, inherit);
      }
    `,
  ];

  @property({ type: String })
  size = ''; // 'md' size seems to be the default one, use 'sm' to see the difference

  @property({ type: String })
  icon = 'communication-alert-circle_solid';

  @property({ type: String })
  color = undefined;

  protected override render(): TemplateResult {
    const classes = {
      icon: true,
      [`icon-${this.size}`]: this.size,
      [`icon--${this.icon}`]: this.icon,
    };

    const styles = { color: this.color ? getColorVar(this.color) : null };

    return html`<span
      style="${styleMap(styles)}"
      class="${classMap(classes)}"
    ></span>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-icon': ZwcIcon;
  }
}
